.infobar{
    background: #2979FF;
    border-radius: 4px 4px 0 0;
    height: 70px;
    width: 100%;
}   
  

.outerContainer{
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #1A1A1D;
    height: 100vh;
}

.innerContainer {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background: #FFFFFF;
  border-radius: 8px;
  height: 80%;
  width: 30%;
}


.chatbox{
  min-height: 70px;
}
   
.form-control{
  border: none;
  border-radius: 0;
  padding: 5%;
  font-size: 1.5em;
}

.form-control:focus {
  border-color:none;
  box-shadow: none;
  outline: none;
}

.btn:focus{
  border-color:none;
  box-shadow: none;
  outline: none;
}

.sendbtn {
    padding: 20px 45px; 
    border: none;
   
}

.messages {
  padding: 5% 0;
  overflow: auto;
  flex: auto;
}

.messageContainer {
  display: flex;
  justify-content: flex-end;
  padding: 0 5%;
  margin-top: 3px;
}

.sentText {
  display: flex;
  align-items: center;
  font-family: Helvetica;
  color: #828282;
  letter-spacing: 0.3px;
}


.messageBox {
  background: #F3F3F3;
  border-radius: 20px;
  padding: 5px 20px;
  color: white;
  display: flex;
  align-items: center;
  max-width: 80%;
}

.messageText {
  width: 100%;
  letter-spacing: 0;
  float: left;
  margin-top: 1px;
  font-size: 1.5em;
  word-wrap: break-word;
}


@media (min-width: 320px) and (max-width: 480px) {

  .infobar{
    height: 50px;
  }

    .innerContainer {
      width: 100%;
      height: 80%;
    }
}
  
@media (min-width: 480px) and (max-width: 1200px) {
    .innerContainer {
      width: 60%;
    }
}